* {
  font-family: Helvetica, Arial, sans-serif;
}

body{
  padding: 60px;
  font-size: 22px;
}

input {
  width: 100%;
  font-size: 1.5rem;
  color:teal;
}

.card {
  border: 1px solid grey;
  border-radius: 10px;
  overflow: hidden;
  margin: 10px;
  align-content: center;
  text-align: center;
}

.card-container{
  display:flex;
  flex-wrap: wrap;
}

.card-image {
  height: 300px;
  width: 500px;
}